  margin-top: 245px; }

.card {
  width: 380px;
  height: 495px;
  margin-left: 20px;
  background: linear-gradient(180deg, #2D2D2D 0%, #1C1C1C 100%);
  border-radius: 16px; }

.card-img-top {
  // width: 289px
  height: 66.87px;
  margin: 60px 51px 0px 40px; }

.card-img-top1 {
  width: 212px;
  height: 75.9px;
